@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,200;0,300;1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#main_nav .container-fluid {
  padding-right: 0px !important;
  padding-left: 0px !important;
}
#main_nav .bg-dark {
  background-color: #f4783d !important;
}
#main_nav .dropdown-menu.show {
  display: block;
  background-color: #f68241;
  width: 200px;
}
#main_nav .navbar-dark .navbar-nav .nav-link {
  color: #fff;
  font-weight: bold;
}
#main_nav .dropdown-item {
  color: #ffffff;
}
#main_nav .dropdown-item:focus,
.dropdown-item:hover {
  background-color: #e47946;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
}

.images_slides {
  margin-top: -24.5px;
  display: block;
  position: relative;
}
.carousel-inner img {
  width: 100%;
  height: 80vh;
  opacity: 0.7;
  -o-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5 ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.carousel-inner img:hover {
  opacity: 1;
}

body {
  height: 3000px;
}
i {
  color: rgb(0, 0, 0);
}
.about-me {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  background-color: #f5f5f5;
  overflow: hidden;
  margin-bottom: 20px;
}
.about-me h1 {
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  text-align: center;
  font-weight: bold !important;
  text-transform: uppercase;
  color: #666;
}
.about {
  background-color: #e3e6fb;
  font-family: "Exo 2", sans-serif;
  padding-bottom: 20px;
}

.about .card {
  box-shadow: 5px 6px 12px #cc94a8;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;

  border-radius: 0px;
}

.about .card img {
  width: 150px;

  height: 150px;

  margin: auto;

  border-radius: 50%;
}

.about .card h2,
.about .card h4 {
  font-family: "Exo 2", sans-serif;
}

.about .about-info {
  background-color: #fff;

  height: 100%;

  padding: 20px;

  -o-box-shadow: 5px 6px 12px #cc94a8;

  -ms-box-shadow: 5px 6px 12px #cc94a8;

  box-shadow: 5px 6px 12px #cc94a8;
}

.about .about-info h2 {
  font-family: Arial, Helvetica, sans-serif;

  font-weight: bold;

  font-family: "Exo 2", sans-serif;

  text-transform: uppercase;
}

.about .about-info h2:before {
  content: "";

  border-bottom: 1px solid #cf4b7c;

  border-width: 4px;

  width: 148px;

  position: absolute;

  top: 58px;
}

.built-with-react {
  width: auto;
  height: 155px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(/mahmoudaosman/static/media/built-with.4bf088b1.png);
  display: block;
  overflow: hidden;
  position: relative;
}

.education {
  width: auto;
  height: auto;
  /* background-color: #e8aa4d; */
  background-color: #c1cee3;
  padding-bottom: 20px;
  position: relative;
  display: block;
}

.education .education-header {
  transform: skew(-35deg);
  -webkit-transform: skew(-35deg);
  -ms-transform: skew(-35deg);
  -moz-transform: skew(-35deg);
  -o-transform: skew(-35deg);
  /* background-color: #ec8876; */
  background-color: #fff;
  overflow: hidden;
  margin-bottom: 20px;
}
.education .education-header h1 {
  -webkit-transform: skew(35deg);
  -ms-transform: skew(35deg);
  -moz-transform: skew(35deg);
  -o-transform: skew(35deg);
  text-align: center;
  color: #666;
  text-transform: uppercase;
}

.education .bacholer,
.education .associate,
.education .certs {
  background-color: #fff;

  height: 100%;

  position: relative;

  display: block;
  -o-box-shadow: 5px 6px 12px #cc94a8;
  -ms-box-shadow: 5px 6px 12px #cc94a8;
  box-shadow: 5px 6px 12px #cc94a8;
}

.education img {
  width: 100%;
  height: auto;
  display: block;
}

